@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light);
@import url(https://fonts.googleapis.com/css2?family=VT323);
@import url(https://fonts.googleapis.com/css2?family=Gabriela);
.btn {
  background-color: var(--button-background-color);
  padding: 5px 15px;
  border: none;
  box-sizing: border-box;
  cursor: pointer;
  border-radius: var(--button-border-radius);
  border: var(--button-border);
  outline: 0;
  color: var(--button-color);
  -webkit-tap-highlight-color: var(--game-tap-highlight-color);
}

.btn.disabled {
  cursor: default;
  outline: 0;
  color: var(--button-color-disabled);
  background-color: var(--button-background-color-disabled);
}

@media (hover: hover) {

  /* only show button hover effects if the device supports it (otherwise it looks silly) */
  .btn:not(.disabled):hover {
    background-color: var(--button-background-color-hover);
  }

}

.btn.hidden {
  display: none;
}

@media only screen and (max-width: 800px) {
  .btn {
    padding: 10px 20px;
  }
}

.board {
  overflow: hidden;
  border-radius: var(--board-border-radius);
  margin-top: 20px !important;
  margin-bottom: 20px !important;
  border: var(--board-border);
  box-shadow: var(--board-shadow);
  background-color: var(--board-background-color);
  padding: var(--board-padding);
}

.board .board-row:first-of-type .square {
  border-top:0; /* hack to hide double border on bottom side of board */
}

.square:first-of-type {
  border-left:0; /* hack to hide double border on left side of board */
}

.square {
  width: var(--square-width);
  height: var(--square-width);
  background: transparent;
  border: var(--square-border);
  border-bottom: none;
  border-right: none;
  float: left;
  padding: 0;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s ease-out;
  border-radius: 0;
  overflow: hidden;
  background-size: var(--square-background-size);
  background-position: center;
  background-repeat: no-repeat;
  box-sizing: content-box;
}

.square:focus {
  outline: none;
}

.square.disabled {
  color: black; /* override button style */
}

@media (hover: hover) {

  /* only show button hover effects if the device supports it (otherwise it looks silly) */

  .square:not(.disabled):hover {
    background-color: var(--button-background-color-hover);
  }

  .square.disabled:hover {
    background-color: var(--button-background-color-disabled);
  }

  .square.winner:hover {
    background-color: var(--square-background-color-win);
  }

}

.square.winner {
  background-color: var(--square-background-color-win);
}

.square.value-x {
  background-image: var(--square-background-image-url-player-x);
}

.square.value-o {
  background-image: var(--square-background-image-url-player-o);
}

.score-board {
  grid-column-start: 2;
  margin: 0 auto 0 auto;
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: var(--scoreboard-gap);
  gap: var(--scoreboard-gap);
  justify-content: center;
}

.score-board .winner {
  /*background-color: lightgreen;
  transition: background-color .5s .1s;*/
}

.score-board .player {
  border: var(--score-board-player-border);
  border-radius: var(--button-border-radius);
  overflow: hidden;
  box-shadow: 2px 3px 7px -5px #0000005c;
  min-width: var(--score-board-player-min-width);
}

.score-board .player div {
  padding: var(--score-board-player-padding);
  text-align: center;
  font-size: var(--score-board-score-font-size);
}

.score-board .player .title {
  height: var(--score-board-score-font-size);
  background-color: var(--score-board-player-background-color);
  background-size: var(--score-board-player-background-size);
  background-position: center;
  background-repeat: no-repeat;
}

.score-board .player .score {
  background-color: var(--score-board-score-background-color);
}

.score-board .player-x .title {
  background-image: var(--score-board-background-image-url-player-x);
}

.score-board .player-o .title {
  background-image: var(--score-board-background-image-url-player-o);
}

.input-stepper {
  display: grid;
  grid-template-columns: auto auto auto;
  align-items: center;
  flex-direction: row;
  align-items: stretch;
  background-color: var(--input-background-color);
  color: var(--input-color);
  border-radius: var(--button-border-radius);
}

.input-stepper label {
  display: flex;
  width: 30px;
  padding-bottom: 1px;
  justify-content: center;
  align-items: center;
  margin: 0 0;
  border: var(--button-border);
  border-right: 0;
  border-left: 0;
  box-sizing: border-box;
  z-index: 0;
  outline: none;
  padding: 5px 15px;
}

.input-stepper button {
  padding: 0 10px 2px 10px;
  border: var(--button-border);
  box-sizing: border-box;
  background-color: transparent;
  font-family: monospace;
  font-size: 22px;
  line-height: 0;
  overflow: hidden;
  z-index: 1;
  border-radius: var(--button-border-radius);
  color: var(--input-color);
}

.input-stepper button:first-of-type {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-right: 0;
}

.input-stepper button:last-of-type {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-left: 0;
}

@media (hover: hover) {

  /* only show button hover effects if the device supports it (otherwise it looks silly) */
  .input-stepper button:not(.disabled):hover {
    background-color: var(--button-background-color-hover);
  }

}

@media only screen and (max-width: 800px) {

  .input-stepper label  {
    padding: 10px 15px;
  }

}

.dropdown {
  background-color: var(--input-background-color);
  padding: 5px 15px;
  border: none;
  box-sizing: border-box;
  cursor: pointer;
  border-radius: var(--button-border-radius);
  border: var(--button-border);
  outline: 0;
  color: var(--input-color);
  background-image: var(--dropdown-background-image);
  background-repeat: no-repeat;
  background-position: center right 10px;
  background-size: 8px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  padding-right: 30px;
}

.dropdown.disabled {
  cursor: default;
  outline: 0;
  color: var(--button-color-disabled);
  background-color: var(--button-background-color-disabled);
}

@media (hover: hover) {

  /* only show button hover effects if the device supports it (otherwise it looks silly) */
  .dropdown:not(.disabled):hover {
    background-color: var(--button-background-color-hover);
  }

}

.dropdown.hidden {
  display: none;
}

@media only screen and (max-width: 800px) {
  .dropdown {
    padding: 10px 20px;
  }
}

* {
  box-sizing: content-box;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  font-family: var(--game-font-family);
  font-size: var(--game-font-size);
  letter-spacing: var(--game-font-letter-spacing);
}

body, html {
  margin: 0;
  height: 100%;
}

#root{
  height: 100%;
}

.game-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow-y: scroll;

  /* show loading icon */
  background-color: #eee;
  background-image: url(/tic-tac-toe/static/media/loading-icon.3a632121.svg);
  background-size: 60px;
  background-position: center;
  background-repeat: no-repeat;
}

.game-wrapper.loaded{
  background-color: var(--game-background-color);
  background-image: none;
}

.loading-icon {
   flex: 1 1;
}

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 30px;
  color: var(--game-color);
  opacity: 0; /* hide while app is loading */
  transition: opacity 1s;
}

.game-wrapper.loaded .game {
  opacity: 1;
}

.game > * {
  margin: 8px;  /* gap between each item */
}

.header {
  align-self: stretch;
  align-content: center;
  background-color: var(--header-background-color);
  padding-bottom: 44px;
  margin: 0;
  margin-bottom: -32px;
  box-shadow: var(--header-shadow);
  display: grid;
  align-items: center;
  justify-items: center;
  background-image: var(--header-background-image);
  background-repeat: no-repeat;
  background-size: var(--header-background-size);
  background-position: var(--header-background-position);
}

  .header > .wrapper {
    display: grid;
    grid-template-columns: minmax(60px, 150px) -webkit-max-content minmax(60px, 150px);
    grid-template-columns: minmax(60px, 150px) max-content minmax(60px, 150px);
    justify-items: center;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
  }

  .header .btn-show-settings {
    justify-self: left;
    align-self: end;
    background-image: var(--settings-button-background-image-url);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 26px;
  }

.settings-panel {
  display: grid;
  margin: 0 !important;
  height:0px;
  transition: height 0.5s;
  overflow: hidden;
  align-self: stretch;
}

  .settings-panel .inner-wrapper {
    padding: 35px 5px 10px;
    justify-self: center;
  }

  .settings-panel .inner-wrapper > label {
    display: block;
    font-weight: bold;
    margin-bottom: 10px;
  }

  .settings-panel .inner-wrapper > .btn {
    margin-top: 10px;
  }

  .settings-panel .wrapper {
    display: grid;
    grid-gap: 10px;
    gap: 10px;
    grid-template-columns: auto auto;
  }

  .settings-panel .wrapper > label {
    align-self: center;
    justify-self: start;
  }

  .settings-panel .wrapper > *:not(label) {
    align-self: center;
    justify-self: end;
  }

.textbox {
  align-self: start;
  display: flex;
  align-items: center;
  flex-direction: row;
  padding: 4px 0;
}

.textbox .err {
  font-size: 14px;
  color: #c30404;
  font-weight: bold;
  max-width: 150px;
  vertical-align: middle ;
}

.textbox label {
}

.textbox input {
  width: 25px;
  font-size: 16px;
  padding: 2px 3px;
  text-align: center;
  max-height: 26px;
  margin: 0 10px;
}

button {
  outline: none;
}

h1 {
  font-size: 50px;
  font-family: var(--header-title-font-family);
  margin: var(--header-title-margin);
  color: var(--header-title-color);
  text-shadow: var(--header-title-shadow);
}

